/* PaletteCreate.css */
.wrapper-palette-create {
  * {
    transition: none
  }

  h1 {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .item {
    width: 400px;
    height: 400px;
    cursor: pointer;
    margin: 0;
    padding: 0;
  }

  .c1 {
    background-color: #ddd
  }

  .c2 {
    background-color: #ccc
  }

  .c3 {
    background-color: #bbb
  }

  .c4 {
    background-color: #aaa
  }

  .hide {
    display: none
  }

  img {
    width: 80px
  }

  .suggest-button {
    margin-top: 20px;
  }

  .sketch-picker {
    display: inline-block;
    float: right
  }

  @media screen and (max-width: 1080px) {
    .center {
      margin: 0;
      padding: 10px;
      width: 240px;
    }
  }

  .sp-container {
    position: absolute!important;
    border-radius: 4px;
    background-color: white;
    border: 1px solid #d7e5ed;
    padding: 0;
    right: 100px!important;
    top: 100px!important;
    left: auto!important;
  }
}
